export const getWeapon = (id?: string | number): string | undefined => {
  if (id === undefined) {
    return undefined
  }
  if (!(id.toString() in weapons)) {
    markMissed({
      type: MissingStuffType.WEAPON,
      what: id.toString()
    })
  }
  return weapons[id] ?? 'kongfu'
}
